.list {
  padding-right: 35px;
}

.list ul {
    display: table;
  margin: 0 auto;
  
}

.list li {
    width: 100%;
    list-style: square;
    border-bottom: 1px solid salmon;
    border-width: 2px;
    padding-top: 10px;
 
}

.list li:hover {
    color: green;
    text-decoration: line-through;
}
